import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

@Injectable()
export class FabricDatePickerCalendarService {

	private readonly activeMonth$ = new Subject<number>();

	private readonly activeYear$ = new Subject<number>();

	onActiveMonth(): Observable<number> {
		return this.activeMonth$.asObservable();
	}

	onActiveYear(): Observable<number> {
		return this.activeYear$.asObservable();
	}

	nextMonth(year: number, month: number): void {
		if (month === 11) {
			this.activeYear$.next(year + 1);
			this.selectMonth(0);
		} else {
			this.selectMonth(month + 1);
		}
	}

	prevMonth(year: number, month: number): void {
		if (month === 0) {
			this.activeYear$.next(year - 1);
			this.selectMonth(11);
		} else {
			this.selectMonth(month - 1);
		}
	}

	selectYear(year: number): void {
		this.activeYear$.next(year);
	}

	selectMonth(month: number): void {
		this.activeMonth$.next(month);
	}
}
