import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	OnDestroy,
	OnInit,
	Renderer2,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { GuiSelectOption } from '../select-option';
import { SelectOptionsGeometryService } from './select-options-geometry.service';
import { SelectOptionsCords } from './select-options.cords';
import { FabricSelectedOptionsRepository } from '../selected-option.repository';
import { FabricThemedComponent } from '../../../common/modal/fabric-themed-component';
import { FabricModalThemeService } from '../../../themes/fabric-modal-theme.service';

@Component({
	template: `
		<div #optionList
			 [class.gui-upward]="canOpenUpward"
			 [class.gui-downward]="!canOpenUpward"
			 [style.left.px]="optionsContainerLeftAttribute"
			 [style.top.px]="optionsContainerTopAttribute"
			 class="gui-options-list">

			<div *ngFor="let option of options"
				 [class.gui-option-selected]="isOptionSelected(option)"
				 [style.width.px]="width"
				 (click)="selectOption(option)"
				 class="gui-option">
				{{getOptionValue(option)}}
			</div>
		</div>
	`,
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class FabricSelectOptionsComponent extends FabricThemedComponent implements OnInit, AfterViewInit, OnDestroy {

	@ViewChild('optionList', { static: false })
	optionListRef: ElementRef;

	options: Array<GuiSelectOption> = [];

	optionsContainerLeftAttribute: number;

	optionsContainerTopAttribute: number;

	width = 100;

	selectedOption: GuiSelectOption;

	canOpenUpward: boolean;

	constructor(private readonly selectOptionsGeometryService: SelectOptionsGeometryService,
				private readonly selectService: FabricSelectedOptionsRepository,
				private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				renderer: Renderer2,
				themeService: FabricModalThemeService) {
		super(elementRef, renderer, themeService);
	}

	ngOnInit(): void {
		this.selectOptionsGeometryService
			.onSelectOptionsCords()
			.pipe(this.takeUntil())
			.subscribe((cords: SelectOptionsCords) => {
				this.optionsContainerTopAttribute = cords.getVerticalPosition();
				this.optionsContainerLeftAttribute = cords.getHorizontalPosition();
				this.canOpenUpward = cords.getCanOpenUpward();
				this.changeDetectorRef.detectChanges();
			});

		this.selectService
			.onSelectedOption()
			.pipe(this.takeUntil())
			.subscribe((selectedOption: GuiSelectOption) => {
				this.selectedOption = selectedOption;
				this.changeDetectorRef.detectChanges();
			});
	}

	ngAfterViewInit() {
		super.ngAfterViewInit();
		this.initOpenAnimation();
	}

	getElementRef(): ElementRef {
		return super.getElementRef();
	}

	detectChanges(): void {
		this.changeDetectorRef.detectChanges();
	}

	selectOption(option: GuiSelectOption): void {
		this.selectService.next(option);
	}

	isOptionSelected(option: GuiSelectOption): boolean {
		return this.selectedOption && option.name === this.selectedOption.name;
	}

	getOptionValue(option: GuiSelectOption): number | string {
		return option.value ? option.value : option.name;
	}

	initOpenAnimation(): void {
		const optionsEl = this.optionListRef.nativeElement;
		this.getRenderer().addClass(optionsEl, 'gui-options-opened');
	}
}
