import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	EventEmitter,
	Inject,
	Input,
	OnChanges,
	OnInit,
	Output,
	PLATFORM_ID,
	Renderer2,
	SimpleChanges,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { GuiSelectOption } from './select-option';
import { SelectOptionsGeometryService } from './options/select-options-geometry.service';
import { FabricSelectedOptionsRepository } from './selected-option.repository';
import { SelectOptionsCords } from './options/select-options.cords';
import { SelectOptionModalService } from './select-option-modal.service';
import { FabricReactive } from '../../common/fabric-reactive';

@Component({
	selector: 'gui-select',
	templateUrl: './select.component.html',
	styleUrls: [
		'./select.ngx.scss',
		'./themes/select.material.ngx.scss',
		'./themes/select.dark.ngx.scss',
		'./themes/select.light.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class.gui-select]': 'true'
	},
	providers: [
		FabricSelectedOptionsRepository,
		SelectOptionsGeometryService,
		SelectOptionModalService
	]
})
export class FabricSelectComponent extends FabricReactive implements OnInit, OnChanges {

	@ViewChild('container', { static: false })
	containerRef: ElementRef;

	@Input()
	options: Array<GuiSelectOption> = [];

	@Input()
	placeholder: string = '...';

	@Input()
	selected: GuiSelectOption;

	@Input()
	width: number = 100;

	@Input()
	disabled: boolean = false;

	@Output()
	optionChanged = new EventEmitter<GuiSelectOption>();

	selectedOption: GuiSelectOption;

	containerText: string;

	private scrollListenerFn: () => void;

	private doNotEmitValues: boolean = false;

	private opened: boolean = false;

	constructor(private readonly selectService: FabricSelectedOptionsRepository,
				private readonly selectOptionsGeometryService: SelectOptionsGeometryService,
				private readonly selectOptionModalService: SelectOptionModalService,
				private readonly changeDetectorRef: ChangeDetectorRef,
				@Inject(PLATFORM_ID) private readonly platformId: any,
				private readonly elementRef: ElementRef,
				private readonly renderer: Renderer2) {
		super();
	}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.placeholder) {
			this.containerText = this.placeholder;
		}

		if (changes.selected) {
			if (this.selected) {
				this.tryToSelect(this.selected);
			}
		}
	}

	ngOnInit(): void {
		this.selectService
			.onSelectedOption()
			.pipe(this.takeUntil())
			.subscribe((selectedOption: GuiSelectOption) => {
				this.emitSelectedOption(selectedOption);
				this.selectedOption = selectedOption;
				this.containerText = selectedOption.value;
				this.doNotEmitValues = false;
				this.changeDetectorRef.detectChanges();
			});

		this.selectOptionsGeometryService
			.onSelectOptionsCords()
			.pipe(
				this.takeUntil()
			)
			.subscribe((cords: SelectOptionsCords) => {

				if (cords.getCanOpenUpward()) {
					this.openUpward();
				} else {
					this.openDownward();
				}
			});
	}

	tryToOpen(event: any): void {

		if (this.isContainerDisabled(event)) {
			event.stopPropagation();
		} else {
			this.open(this.options, this.width);
			this.toggleOptions(!this.opened);
			this.changeDetectorRef.detectChanges();
		}
	}

	clickOutside(event: any): void {
		if (this.isContainerClicked(event)) {
			this.toggleOptions(false);
		}
	}

	toggleOptions(opened: boolean) {
		this.opened = opened;
		this.maintainOptionsListPosition();
		if (!this.opened) {
			this.closeOptions();
		}
	}

	isOptionSelected(option: GuiSelectOption): boolean {
		return this.selectedOption && option.name === this.selectedOption.name;
	}

	getOptionValue(option: GuiSelectOption): string {
		if (option) {
			return option.value ? option.value : option.name;
		}
	}

	private open(options: Array<GuiSelectOption>, width: number): void {
		this.closeSelect();
		this.selectOptionModalService.open(options, width);
		this.selectOptionsGeometryService.nextCords(this.containerRef);
	}

	private tryToSelect(option: GuiSelectOption): void {
		const canSelect = this.selectedOption ? option.name !== this.selectedOption.name : true;

		if (canSelect) {
			this.doNotEmitValues = true;
			this.selectService.next(option);
			this.selectedOption = option;
			this.containerText = this.getOptionValue(option);
		}
	}

	private maintainOptionsListPosition(): void {
		if (isPlatformBrowser(this.platformId)) {
			if (this.opened) {
				this.scrollListenerFn = this.renderer.listen('window', 'scroll', () => {
					this.selectOptionsGeometryService.nextCords(this.containerRef);
				});
			} else {
				if (this.scrollListenerFn) {
					this.scrollListenerFn();
				}
			}
		}
	}

	private closeSelect(): void {
		this.selectOptionModalService.closeOptions();
	}

	private emitSelectedOption(option: GuiSelectOption): void {
		if (!this.doNotEmitValues) {
			if (!this.isOptionSelected(option)) {
				this.optionChanged.emit(option);
			}
		}
	}

	private openDownward(): void {
		this.addClass('gui-options-opened');
		this.addClass('gui-downward');
		this.removeClass('gui-upward');
	}

	private openUpward(): void {
		this.addClass('gui-options-opened');
		this.addClass('gui-upward');
		this.removeClass('gui-downward');
	}

	private closeOptions(): void {
		const optionsElHasOpenClass = this.elementRef.nativeElement.classList.contains('gui-options-opened');

		if (optionsElHasOpenClass) {
			this.removeClass('gui-options-opened');
			this.closeSelect();
		}
	}

	private isContainerClicked(event: any): boolean {
		return !this.elementRef.nativeElement.contains(event.target);
	}

	private isContainerDisabled(event: any): boolean {
		return event.target.classList.contains('gui-disabled');
	}

	private addClass(className: string): void {
		this.renderer.addClass(this.elementRef.nativeElement, className);
	}

	private removeClass(className: string): void {
		this.renderer.removeClass(this.elementRef.nativeElement, className);
	}
}
