import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { Theme } from './theme';
import { Observable } from 'rxjs/';

@Injectable()
export class FabricModalThemeService {

	private readonly fabricTheme$ = new BehaviorSubject<Theme>(Theme.FABRIC);

	onTheme(): Observable<Theme> {
		return this.fabricTheme$.asObservable();
	}

	changeTheme(theme: string | Theme): void {
		const fabricTheme = typeof theme === 'string' ? this.convertToTheme(theme) : theme;
		this.fabricTheme$.next(fabricTheme);
	}

	convertToTheme(theme: string): Theme {
		switch (theme.toLowerCase()) {

			case 'fabric':
				return Theme.FABRIC;

			case 'material':
				return Theme.MATERIAL;

			case 'generic':
				return Theme.GENERIC;

			case 'light':
				return Theme.LIGHT;

			case 'dark':
				return Theme.DARK;
		}
	}

}
