import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Inject,
	Input,
	PLATFORM_ID,
	QueryList,
	Renderer2,
	ViewChild,
	ViewChildren,
	ViewEncapsulation
} from '@angular/core';
import { Direction } from '../../common/icons/arrow-icon/direction';
import { fromEvent } from 'rxjs';
import { FabricReactive } from '../../common/fabric-reactive';
import { isPlatformBrowser } from '@angular/common';
import { GuiTabItem } from './fabric-tab-item';

@Component({
	selector: 'gui-tab',
	templateUrl: './tab.component.html',
	styleUrls: [
		'./tab.ngx.scss',
		'./themes/tab.material.ngx.scss',
		'./themes/tab.dark.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class.gui-tab]': 'true'
	}
})
export class FabricTabComponent extends FabricReactive implements AfterViewInit {

	@ViewChild('tab', { static: false })
	tabRef: ElementRef;

	@ViewChild('tabItem', { static: false })
	tabItemRef: ElementRef;

	@ViewChildren('tabMenuList')
	tabMenuList: QueryList<ElementRef>;

	@Input()
	menu: Array<string | GuiTabItem>;

	@Input()
	active: string;

	@Input()
	scrollActive: boolean;

	Direction = Direction;

	private listPosition: number = 0;
	private menuListWidth: number = 0;
	private readonly scrollAmount = 60;

	private readonly ACTIVE_TAB_CLASS_NAME = 'gui-active';

	constructor(private renderer: Renderer2,
				private changeDetectorRef: ChangeDetectorRef,
				@Inject(PLATFORM_ID) private platformId: any) {
		super();
	}

	ngAfterViewInit() {
		this.toggleTab(this.active);
		this.calculateMenuWidth();
		this.showMenuArrows();
		this.checkIfMenuFitsOnResize();
	}

	toggleTab(tab: string): void {
		this.removeActive();
		this.setActive(tab);
	}

	isSvg(item: string | GuiTabItem): boolean {
		return typeof item === 'object';
	}

	getTabName(item: string | GuiTabItem): string {
		return typeof item === 'object' ? item.name : item;
	}

	scrollTabList(scrollRightClicked: boolean): void {
		const listWidth = this.tabRef.nativeElement.querySelector('.gui-tab-menu-list').offsetWidth,
			menuOverflow = this.menuListWidth - listWidth;

		if (scrollRightClicked && menuOverflow > this.listPosition) {
			this.listPosition += this.scrollAmount;
		} else if (!scrollRightClicked && this.listPosition > 0) {
			this.listPosition -= this.scrollAmount;
		}

		this.tabRef.nativeElement.querySelector('.' + 'gui-tab-menu-list').scrollLeft = this.listPosition;
	}

	private setActive(tab: string | GuiTabItem): void {
		if (typeof tab === 'object') {
			tab = tab.name;
		}

		const navMenuTabEl = this.tabRef.nativeElement.querySelector('[data-tab="' + tab + '"]'),
			navTabItemEl = this.tabItemRef.nativeElement.querySelector('[data-tab="' + tab + '"]');

		this.addClass(navMenuTabEl, this.ACTIVE_TAB_CLASS_NAME);
		this.addClass(navTabItemEl, this.ACTIVE_TAB_CLASS_NAME);
	}

	private removeActive(): void {
		const navMenuTabEl = this.tabRef.nativeElement.querySelector('.' + this.ACTIVE_TAB_CLASS_NAME),
			navTabItemEl = this.tabItemRef.nativeElement.querySelector('.' + this.ACTIVE_TAB_CLASS_NAME);

		this.removeClass(navMenuTabEl, this.ACTIVE_TAB_CLASS_NAME);
		this.removeClass(navTabItemEl, this.ACTIVE_TAB_CLASS_NAME);
	}

	private addClass(element: any, name: string) {
		if (element) {
			this.renderer.addClass(element, name);
		}
	}

	private removeClass(element: any, name: string) {
		if (element) {
			this.renderer.removeClass(element, name);
		}
	}

	private checkIfMenuFitsOnResize(): void {
		if (isPlatformBrowser(this.platformId)) {

			fromEvent(window, 'resize')
				.pipe(this.takeUntil())
				.subscribe(() => this.showMenuArrows());
		}
	}

	private calculateMenuWidth(): void {
		this.menuListWidth = 0;
		this.tabMenuList
			.forEach((listItem: ElementRef) => {
				this.menuListWidth += listItem.nativeElement.offsetWidth;
			});
	}

	private showMenuArrows(): void {
		const menuWidth = this.tabRef.nativeElement.querySelector('.gui-tab-menu').offsetWidth;

		this.scrollActive = menuWidth < this.menuListWidth;
		this.changeDetectorRef.detectChanges();
	}

}
