import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ViewEncapsulation } from '@angular/core';
import { FabricMessageService } from './fabric-message.service';

@Component({
	selector: 'gui-message',
	template: `
		<div class=" gui-message">
			{{text}}
			<button (click)="close()">X</button>
		</div>
	`,
	styleUrls: [
		'./fabric-message.ngx.scss'
	],
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class FabricMessageComponent {
	text: string;

	constructor(private readonly messageService: FabricMessageService,
				private readonly changeDetectorRef: ChangeDetectorRef) {
	}

	detectChanges(): void {
		this.changeDetectorRef.detectChanges();
	}

	close(): void {
		this.messageService.close();
	}
}
