import {
	ApplicationRef,
	ComponentFactoryResolver,
	ComponentRef,
	ElementRef,
	EmbeddedViewRef,
	Inject,
	Injectable,
	Injector,
	OnDestroy,
	Type
} from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { fromEvent } from 'rxjs';
import { filter } from 'rxjs/operators';

import { FabricDrawerComponent } from './fabric-drawer.component';
import { Theme } from '../../themes/theme';
import { themeToken } from '../../themes/theme.token';
import { DialogService } from '../common/dialog.service';
import { FabricNestedDialogComponent } from '../common/fabric.nested-dialog.component';


@Injectable()
export class FabricDrawerService extends DialogService implements OnDestroy {

	drawerRef: ComponentRef<FabricNestedDialogComponent> = null;

	constructor(private componentFactoryResolver: ComponentFactoryResolver,
				private applicationRef: ApplicationRef,
				private injector: Injector,
				@Inject(DOCUMENT) private document: any) {
		super();
	}

	ngOnDestroy() {
		this.removeDrawer();
		super.ngOnDestroy();
	}

	open(element: ElementRef, component: Type<FabricNestedDialogComponent>,
		 config?: { injector?: Injector, theme?: Theme, closeOnClickOutside?: boolean }): void {
		if (event) {
			event.stopPropagation();
		}

		if (!this.drawerRef) {

			let theme = Theme.FABRIC,
				parentInjector = this.injector,
				closeOnClickOutside = false;

			if (config && config.theme) {
				theme = config.theme;
			}

			if (config && config.injector) {
				parentInjector = config.injector;
			}

			if (config && config.closeOnClickOutside) {
				closeOnClickOutside = config.closeOnClickOutside;
			}

			const injector = Injector.create({
				providers: [{
					provide: themeToken,
					useValue: theme
				}],
				parent: parentInjector
			});

			this.createAndAppend(element, component, injector, closeOnClickOutside);
			this.closeOnEscKey();
		}
	}

	close(): void {
		this.removeDrawer();
	}

	private closeOnEscKey(): void {
		const close$ = fromEvent<any>(this.document, 'keyup');

		close$
			.pipe(
				filter((key) => key.code === 'Escape'),
				this.takeUntil()
			)
			.subscribe(() => this.close());
	}

	private createAndAppend(element: ElementRef,
							component: Type<FabricNestedDialogComponent>,
							injector: Injector,
							closeOnClickOutside: boolean): void {

		const componentRef = this.componentFactoryResolver
								 .resolveComponentFactory(FabricDrawerComponent)
								 .create(injector);

		componentRef.instance.dialogNestedComponent = component;
		componentRef.instance.closeOnClickOutside = closeOnClickOutside;
		componentRef.changeDetectorRef.detectChanges();

		this.applicationRef.attachView(componentRef.hostView);

		const domDrawerElement = (componentRef.hostView as EmbeddedViewRef<any>)
			.rootNodes[0] as HTMLElement;

		element.nativeElement.appendChild(domDrawerElement);

		this.drawerRef = componentRef;
	}

	private removeDrawer(): void {
		if (this.drawerRef) {
			this.applicationRef.detachView(this.drawerRef.hostView);
			this.drawerRef.destroy();
			this.drawerRef = null;
		}
	}

}
