import { FabricReactive } from '../fabric-reactive';
import { AfterViewInit, ElementRef, Renderer2 } from '@angular/core';

import { FabricModalThemeService } from '../../themes/fabric-modal-theme.service';
import { distinctUntilChanged } from 'rxjs/operators';
import { Theme } from '../../themes/theme';


export abstract class FabricThemedComponent extends FabricReactive implements AfterViewInit {

	private static readonly CSS_CLASS_PREFIX = 'gui-';

	protected constructor(private readonly elementRef: ElementRef,
						  private readonly renderer: Renderer2,
						  private readonly themeService: FabricModalThemeService) {
		super();
	}

	ngAfterViewInit() {
		this.themeService
			.onTheme()
			.pipe(
				distinctUntilChanged(),
				this.takeUntil()
			)
			.subscribe((theme: Theme) => {
				this.addTheme(theme);
			});
	}

	protected addTheme(theme: Theme): void {
		this.removeThemes();
		const cssClass = FabricThemedComponent.CSS_CLASS_PREFIX + theme.toLowerCase();

		this.renderer.addClass(this.elementRef.nativeElement, cssClass);
	}

	protected removeThemes(): void {
		const themes = Object.keys(Theme)
							 .map((key: string) => Theme[key].toLowerCase())
							 .filter((val) => !Number.isInteger(val));

		themes.forEach((theme: string) => {
			const cssClass = FabricThemedComponent.CSS_CLASS_PREFIX + theme;
			this.renderer.removeClass(this.elementRef.nativeElement, cssClass);
		});
	}

	protected getElementRef(): ElementRef {
		return this.elementRef;
	}

	protected getRenderer(): Renderer2 {
		return this.renderer;
	}
}

