import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ComponentFactoryResolver,
	ElementRef,
	forwardRef,
	Inject,
	Injector,
	OnDestroy,
	OnInit,
	Renderer2,
	Type,
	ViewChild,
	ViewContainerRef,
	ViewEncapsulation
} from '@angular/core';

import { FabricInlineDialogService } from './fabric-inline-dialog.service';
import { InlineDialogGeometryService } from './fabric-inline-dialog-geometry.service';
import { themeToken } from '../../themes/theme.token';
import { Theme } from '../../themes/theme';
import { InlineDialogCords } from './inline-dialog-cords';
import { FabricNestedDialogComponent } from '../common/fabric.nested-dialog.component';
import { FabricThemedComponent } from '../../common/modal/fabric-themed-component';
import { FabricModalThemeService } from '../../themes/fabric-modal-theme.service';


@Component({
	templateUrl: 'fabric-inline-dialog.component.html',
	styleUrls: [
		'./fabric-inline-dialog.ngx.scss',
		'./themes/fabric-inline-dialog.dark.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FabricInlineDialogComponent extends FabricThemedComponent implements OnInit, AfterViewInit, OnDestroy {

	@ViewChild('container', { read: ViewContainerRef, static: false })
	container: ViewContainerRef;

	customClass: string;

	inlineDialogNestedComponent: Type<FabricNestedDialogComponent>;

	inlineDialogNestedInjector: Injector;

	dialogTopAttribute: number;

	dialogLeftAttribute: number;

	constructor(private readonly componentFactoryResolver: ComponentFactoryResolver,
				private readonly changeDetectorRef: ChangeDetectorRef,
				@Inject(forwardRef(() => FabricInlineDialogService)) private readonly inlineDialogService: FabricInlineDialogService,
				private readonly elRef: ElementRef,
				private readonly inlineDialogGeometryService: InlineDialogGeometryService,
				renderer: Renderer2,
				themeService: FabricModalThemeService,
				@Inject(themeToken) theme: Theme) {
		super(elRef, renderer, themeService);
	}

	ngOnInit() {
		this.inlineDialogGeometryService
			.observeInlineDialogCords()
			.pipe(
				this.takeUntil()
			)
			.subscribe((inlineDialogCords: InlineDialogCords) => {
				this.dialogTopAttribute = inlineDialogCords.getVerticalPosition();
				this.dialogLeftAttribute = inlineDialogCords.getHorizontalPosition();
				this.changeDetectorRef.detectChanges();
			});
	}

	ngAfterViewInit() {
		super.ngAfterViewInit();
		this.createNestedComponent(this.inlineDialogNestedComponent);
		this.inlineDialogGeometryService.changeGeometry(this.elRef);
		this.changeDetectorRef.detectChanges();
	}

	ngOnDestroy() {
		this.unsubscribe();
	}

	clickOutside(event: any): void {
		if (this.isContainerClicked(event)) {
			this.inlineDialogService.close();
		}
	}

	private isContainerClicked(event: any): boolean {
		return !this.elRef.nativeElement.contains(event.target);
	}

	private createNestedComponent(component: Type<FabricNestedDialogComponent>): void {
		const componentFactory = this.componentFactoryResolver.resolveComponentFactory(component);

		if (this.inlineDialogNestedInjector) {
			this.container.createComponent(componentFactory, null, this.inlineDialogNestedInjector);
		} else {
			this.container.createComponent(componentFactory);
		}
		this.changeDetectorRef.detectChanges();
	}
}
