import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { FabricDatePickerComposition } from './models/fabric-date-picker-composition';
import { FabricDatePickerOptions } from './models/fabric-date-picker-options';

@Injectable()
export class FabricDatePickerCompositionService {
	private static readonly DEFAULT_COMPOSITION = FabricDatePickerComposition.DATE_PICKER;

	private readonly datePickerFormat$ = new BehaviorSubject<FabricDatePickerComposition>(FabricDatePickerCompositionService.DEFAULT_COMPOSITION);

	onComposition(): Observable<FabricDatePickerComposition> {
		return this.datePickerFormat$.asObservable();
	}

	next(datePipeOptions: FabricDatePickerOptions): void {
		this.datePickerFormat$.next(this.getComposition(datePipeOptions));
	}

	private getComposition(datePipeOptions: FabricDatePickerOptions): FabricDatePickerComposition {

		let composition: FabricDatePickerComposition,
			timerPicker = datePipeOptions.timePicker;

		const showHours = timerPicker.hours,
			showMinutes = timerPicker.minutes,
			showSeconds = timerPicker.seconds,
			isMeridian = timerPicker.meridian,
			showDatePicker = datePipeOptions.datePicker === undefined ? true : datePipeOptions.datePicker,
			showTimePicker = showHours || showMinutes || showSeconds;

		if (showDatePicker) {
			composition = composition | FabricDatePickerComposition.DATE_PICKER;
		}

		if (showTimePicker) {
			composition = composition | FabricDatePickerComposition.TIME_PICKER;
		}

		if (isMeridian) {
			composition = composition | FabricDatePickerComposition.TIME_PICKER_MERIDIAN;
		}

		if (showHours) {
			composition = composition | FabricDatePickerComposition.TIME_PICKER_HOURS;
		}

		if (showMinutes) {
			composition = composition | FabricDatePickerComposition.TIME_PICKER_MINUTES;
		}

		if (showSeconds) {
			composition = composition | FabricDatePickerComposition.TIME_PICKER_SECONDS;
		}

		return composition;
	}
}
