import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FabricDatePickerService } from '../fabric-date-picker.service';
import { FabricDatePickerWeeks } from './weeks/fabric-date-picker.weeks';
import { FabricDatePickerYears } from './years/fabric-date-picker.years';
import { FabricReactive } from '../../../common/fabric-reactive';
import { FabricCalendarView } from '../models/fabric-calendar-view';
import { FabricDatePickerCalendarViewService } from './fabric-date-picker-calendar-view.service';
import { FabricDatePickerCalendarService } from './fabric-date-picker-calendar.service';
import { FabricDatePickerYearsService } from './years/fabric-date-picker-years.service';
import { FabricDatePickerCompositionService } from '../fabric-date-picker-composition.service';
import { FabricDatePickerComposition } from '../models/fabric-date-picker-composition';


@Component({
	selector: 'gui-date-picker-toggle',
	templateUrl: 'fabric-date-picker-calendar.component.html',
	styleUrls: [
		'./fabric-date-picker-calendar.ngx.scss'
	],
	host: {
		'[class.gui-date-picker-calendar]': 'true'
	},
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FabricDatePickerCalendarComponent extends FabricReactive implements OnInit {

	/** Cells representing days in weeks of the active month. */
	weeks: Array<Array<Date>>;

	/** Year cells displayed in years view. */
	years: Array<Array<number>>;

	selectedDate: Date;

	activeMonth: number;

	activeYear: number;

	datePickerComposition: FabricDatePickerComposition;

	FabricDatePickerComposition = FabricDatePickerComposition;

	FabricCalendarView = FabricCalendarView;

	fabricCalendarView: FabricCalendarView = FabricCalendarView.DAYS;

	constructor(private readonly datePickerService: FabricDatePickerService,
				private readonly datePickerFormatService: FabricDatePickerCompositionService,
				private readonly datePickerWeeks: FabricDatePickerWeeks,
				private readonly datePickerYears: FabricDatePickerYears,
				private readonly datePickerYearsService: FabricDatePickerYearsService,
				private readonly calendarService: FabricDatePickerCalendarService,
				private readonly calendarViewService: FabricDatePickerCalendarViewService,
				private readonly changeDetectorRef: ChangeDetectorRef) {
		super();
	}

	ngOnInit() {
		this.datePickerFormatService
			.onComposition()
			.pipe(this.takeUntil())
			.subscribe((datePickerComposition: FabricDatePickerComposition) => {
				this.datePickerComposition = datePickerComposition;
			});

		this.calendarService
			.onActiveMonth()
			.pipe(this.takeUntil())
			.subscribe((month: number) => {
				this.activeMonth = month;
				this.calculateDatePickerData();
				this.changeDetectorRef.detectChanges();
			});

		this.calendarService
			.onActiveYear()
			.pipe(this.takeUntil())
			.subscribe((year: number) => {
				this.activeYear = year;
				this.calculateDatePickerData();
				this.changeDetectorRef.detectChanges();
			});

		this.datePickerService
			.observeSelectedDate()
			.pipe(this.takeUntil())
			.subscribe((date: Date) => {
				this.selectedDate = date;
				this.activeYear = date.getFullYear();
				this.activeMonth = date.getMonth();
			});

		this.datePickerYearsService
			.onYears()
			.pipe(this.takeUntil())
			.subscribe((years: Array<Array<number>>) => {
				this.years = years;
				this.changeDetectorRef.detectChanges();
			});

		this.calendarViewService
			.onActiveView()
			.pipe(this.takeUntil())
			.subscribe((fabricCalendarView: FabricCalendarView) => {
				this.fabricCalendarView = fabricCalendarView;
				this.changeDetectorRef.detectChanges();
			});

		this.calculateDatePickerData();
	}

	getCalendarView(): FabricCalendarView {
		if (event) {
			event.stopPropagation();
		}

		return this.fabricCalendarView;
	}

	isVisible(activeComposition: FabricDatePickerComposition, checkedComposition: FabricDatePickerComposition): boolean {
		return !!(activeComposition & checkedComposition);
	}

	private calculateDatePickerData(): void {
		this.weeks = this.datePickerWeeks.getDaysInMonths(this.activeYear, this.activeMonth);
		this.years = this.datePickerYears.getYears(this.activeYear);
	}
}
