import {
	ApplicationRef,
	ComponentFactoryResolver,
	ComponentRef,
	ElementRef,
	EmbeddedViewRef,
	Inject,
	Injectable,
	Injector,
	OnDestroy,
	Type
} from '@angular/core';
import { DOCUMENT } from '@angular/common';

import { FabricInlineDialogComponent } from './fabric-inline-dialog.component';
import { InlineDialogGeometryService } from './fabric-inline-dialog-geometry.service';
import { Theme } from '../../themes/theme';
import { themeToken } from '../../themes/theme.token';
import { FabricNestedDialogComponent } from '../common/fabric.nested-dialog.component';
import { filter, takeUntil } from 'rxjs/operators';
import { BehaviorSubject, fromEvent, Observable, Subject } from 'rxjs';
import { FabricPlacement } from '../../common/modal/cords/fabric-placement';


@Injectable()
export class FabricInlineDialogService implements OnDestroy {

	private inlineDialogRef: ComponentRef<FabricNestedDialogComponent> = null;

	private opened = false;

	private opened$ = new BehaviorSubject(false);
	private readonly unsub$ = new Subject();

	constructor(private componentFactoryResolver: ComponentFactoryResolver,
				private applicationRef: ApplicationRef,
				private injector: Injector,
				@Inject(DOCUMENT) private document: any,
				private inlineDialogGeometryService: InlineDialogGeometryService) {

	}

	ngOnDestroy() {
		this.removeInlineDialog();
	}

	open(element: ElementRef, component: Type<FabricNestedDialogComponent>,
		 config?: {
			 injector?: Injector,
			 placement?: FabricPlacement,
			 offset?: number,
			 theme?: Theme,
			 customClass?: string
		 }): void {

		if (event) {
			event.stopPropagation();
		}

		if (!this.inlineDialogRef) {

			let parentInjector = this.injector,
				placement = FabricPlacement.BOTTOM,
				offset = 0,
				theme = Theme.FABRIC,
				customClass;

			if (config && config.injector) {
				parentInjector = config.injector;
			}

			if (config && config.placement) {
				placement = config.placement;
			}

			if (config && config.offset) {
				offset = config.offset;
			}

			if (config && config.theme) {
				theme = config.theme;
			}

			if (config && config.customClass) {
				customClass = config.customClass;
			}

			const injector = Injector.create({
				providers: [{
					provide: themeToken,
					useValue: theme
				}],
				parent: parentInjector
			});

			this.setOpened(true);
			this.appendInlineDialogToElement(component, injector, customClass);
			this.inlineDialogGeometryService.getInlineDialogCords(element, placement, offset);
			this.closeOnEscKey();
		} else {
			this.close();
		}
	}

	close(): void {
		this.removeInlineDialog();
		this.unsub$.next();
		this.unsub$.complete();
		this.setOpened(false);
	}

	isOpened(): boolean {
		return this.opened;
	}

	onOpened(): Observable<boolean> {
		return this.opened$.asObservable();
	}

	private appendInlineDialogToElement(component: any, injector: Injector, customClass: string): void {

		const componentRef = this.componentFactoryResolver
								 .resolveComponentFactory(FabricInlineDialogComponent)
								 .create(injector);

		componentRef.instance.customClass = customClass;

		componentRef.instance.inlineDialogNestedComponent = component;

		componentRef.changeDetectorRef.detectChanges();

		this.applicationRef.attachView(componentRef.hostView);

		const domDialogElement = (componentRef.hostView as EmbeddedViewRef<any>)
			.rootNodes[0] as HTMLElement;

		this.document.body.appendChild(domDialogElement);

		this.inlineDialogRef = componentRef;
	}

	private removeInlineDialog(): void {
		if (this.inlineDialogRef) {
			this.applicationRef.detachView(this.inlineDialogRef.hostView);
			this.inlineDialogRef.destroy();
			this.inlineDialogRef = null;
		}
	}

	private setOpened(opened: boolean): void {
		this.opened = opened;
		this.opened$.next(this.opened);
	}

	private closeOnEscKey(): void {
		const close$ = fromEvent<any>(this.document, 'keyup');

		close$
			.pipe(
				filter((key) => key.code === 'Escape'),
				takeUntil(this.unsub$)
			)
			.subscribe(() => this.close());
	}
}
