import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { FabricCalendarView } from '../models/fabric-calendar-view';

@Injectable()
export class FabricDatePickerCalendarViewService {

	private activeView$ = new Subject<FabricCalendarView>();

	onActiveView(): Observable<FabricCalendarView> {
		return this.activeView$.asObservable();
	}

	switchView(viewName: FabricCalendarView): void {
		this.activeView$.next(viewName);
	}

}
