import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { GuiSelectOption } from './select-option';

@Injectable()
export class FabricSelectedOptionsRepository {

	private readonly selectedOption$ = new ReplaySubject<GuiSelectOption>(1);

	onSelectedOption(): Observable<GuiSelectOption> {
		return this.selectedOption$.asObservable();
	}

	next(option: GuiSelectOption): void {
		this.selectedOption$.next(option);
	}

}
