import { ElementRef } from '@angular/core';
import { FabricPlacement } from './fabric-placement';
import { FabricGeometry } from './fabric-geometry';

export abstract class FabricCords extends FabricGeometry {

	private verticalPosition: number;

	private horizontalPosition: number;

	protected constructor(window: Window,
						  invokerElementRef: ElementRef,
						  invokedElementRef: ElementRef,
						  containerClassName: string,
						  placement: FabricPlacement,
						  offset?: number) {
		super(invokedElementRef, containerClassName);
		this.calculateCords(window, invokerElementRef, placement, offset);
	}

	getVerticalPosition(): number {
		return this.verticalPosition;
	}

	getHorizontalPosition(): number {
		return this.horizontalPosition;
	}

	private calculateCords(window: Window,
						   invokerElementRef: ElementRef,
						   placement: FabricPlacement,
						   offset: number): void {

		const elementRect = invokerElementRef.nativeElement.getBoundingClientRect(),
			elementBottom = window.pageYOffset + elementRect.bottom,
			elementLeft = window.pageXOffset + elementRect.left,
			elementRight = window.pageXOffset + elementRect.right,
			elementTop = window.pageYOffset + elementRect.top,
			topCords = elementTop + offset - this.getHeight();

		switch (placement) {

			case FabricPlacement.BOTTOM:
				this.horizontalPosition = elementLeft;
				this.verticalPosition = elementBottom + offset;
				break;

			case FabricPlacement.TOP:
				this.horizontalPosition = elementLeft;
				this.verticalPosition = topCords;
				break;

			case FabricPlacement.BEFORE:
				this.horizontalPosition = elementRight + offset - this.getWidth();
				this.verticalPosition = elementBottom;
				break;

			case FabricPlacement.AFTER:
				this.horizontalPosition = elementLeft + offset;
				this.verticalPosition = elementBottom;
				break;

			default :
				this.horizontalPosition = elementLeft;
				this.verticalPosition = elementBottom + offset;
		}

		this.calculateDirection(invokerElementRef, window, topCords);
	}

	private calculateDirection(invokerElementRef: ElementRef, window: Window, topCords: number): void {
		const windowHeightWithOffset = window.innerHeight + window.pageYOffset,
			windowWidthWithOffset = window.innerWidth + window.pageXOffset,
			invokerElementWidth = invokerElementRef.nativeElement.offsetWidth,
			invokedElementHeight = this.getHeight(),
			invokedElementWidth = this.getWidth();

		const invokedElementDoesNotFitHorizontally = (windowWidthWithOffset - this.horizontalPosition - invokedElementWidth) < 0,
			invokedElementDoesNotFitVertically = (windowHeightWithOffset - this.verticalPosition - invokedElementHeight) < 0;

		if (invokedElementDoesNotFitHorizontally) {
			this.horizontalPosition -= invokedElementWidth - invokerElementWidth;
		}

		if (invokedElementDoesNotFitVertically) {
			this.verticalPosition = topCords;
		}
	}
}
