import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewEncapsulation } from '@angular/core';

@Component({
	selector: 'gui-rating',
	templateUrl: './fabric-rating.component.html',
	styleUrls: ['./fabric-rating.ngx.scss'],
	host: {
		'[class.gui-rating]': 'true'
	},
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class FabricRatingComponent implements OnChanges, OnInit {
	@Input()
	starsLength: number;

	@Input()
	rating: number = 3;

	@Output()
	onRatingChange = new EventEmitter();

	stars: Array<number> = [];

	previewRating: number = 0;

	ngOnChanges(changes: SimpleChanges) {
		if (changes.starsLength) {
			if (this.starsLength) {
				this.createStarsArray(this.starsLength);
			}
		}
	}

	ngOnInit() {
		if (this.stars.length === 0) {
			this.createStarsArray(5);
		}
	}

	isRating(starNumber: number, rating: number): boolean {
		return !(rating >= starNumber);
	}

	changeRating(star: number): void {
		this.rating = star;
		this.onRatingChange.emit(star);
	}

	changePreviewRating(star: number): void {
		this.previewRating = star + 1;
	}

	createStarsArray(length: number): void {
		for (let i = 1; i <= length; i++) {
			this.stars.push(i);
		}
	}
}
