import { ElementRef } from '@angular/core';
import { FabricCords } from '../../common/modal/cords/fabric-cords';
import { FabricPlacement } from '../../common/modal/cords/fabric-placement';

export class InlineDialogCords extends FabricCords {

	private static defaultInlineDialogOffset: number = 8;

	constructor(private invokerElementRef: ElementRef,
				private invokedElementRef: ElementRef,
				private window: Window,
				private placement?: FabricPlacement,
				private inlineDialogOffset: number = InlineDialogCords.defaultInlineDialogOffset) {
		super(
			window,
			invokerElementRef,
			invokedElementRef,
			'gui-inline-dialog-wrapper',
			placement,
			inlineDialogOffset
		);
	}

}
