import { ElementRef, Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { SelectOptionsCords } from './select-options.cords';
import { Observable, ReplaySubject } from 'rxjs';
import { isPlatformBrowser } from '@angular/common';
import { SelectOptionsGeometry } from './select-geometry';

@Injectable()
export class SelectOptionsGeometryService {

	private selectContainerGeometry: SelectOptionsGeometry;

	private readonly selectOptionsCords$ = new ReplaySubject<SelectOptionsCords>(1);

	constructor(@Inject(PLATFORM_ID) private readonly platformId: any) {
	}

	onSelectOptionsCords(): Observable<SelectOptionsCords> {
		return this.selectOptionsCords$.asObservable();
	}

	setGeometry(selectContainerRef: ElementRef): void {
		this.selectContainerGeometry = new SelectOptionsGeometry(selectContainerRef);
	}

	nextCords(element: ElementRef): void {

		if (isPlatformBrowser(this.platformId)) {
			const selectOptionsCords =
				new SelectOptionsCords(
					element,
					this.selectContainerGeometry,
					window);

			this.selectOptionsCords$.next(selectOptionsCords);
		}
	}
}
