import { ElementRef, Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { Observable, Subject } from 'rxjs';
import { InlineDialogCords } from './inline-dialog-cords';
import { FabricPlacement } from '../../common/modal/cords/fabric-placement';


@Injectable()
export class InlineDialogGeometryService {

	private inlineDialogGeometry: ElementRef;
	private inlineDialogState$ = new Subject<InlineDialogCords>();

	constructor(@Inject(PLATFORM_ID) private platformId: any) {
	}

	observeInlineDialogCords(): Observable<InlineDialogCords> {
		return this.inlineDialogState$.asObservable();
	}

	changeGeometry(inlineDialogRef: ElementRef): void {
		this.inlineDialogGeometry = inlineDialogRef;
	}

	getInlineDialogCords(invokerElementRef: ElementRef, placement?: FabricPlacement, offset?: number): void {

		if (isPlatformBrowser(this.platformId)) {
			const inlineDialogCords =
				new InlineDialogCords(
					invokerElementRef,
					this.inlineDialogGeometry,
					window,
					placement,
					offset);

			this.inlineDialogState$.next(inlineDialogCords);
		}
	}
}
