import { ElementRef } from '@angular/core';
import { SelectOptionsGeometry } from './select-geometry';

export class SelectOptionsCords {
	private static readonly BORDER_WIDTH = 1;

	private verticalPosition: number;

	private horizontalPosition: number;

	private canOpenUpward: boolean;

	constructor(private readonly element: ElementRef,
				private readonly selectContainerGeometry: SelectOptionsGeometry,
				private readonly window: Window) {
		this.calculateCords(element, selectContainerGeometry);
	}

	getVerticalPosition(): number {
		return this.verticalPosition;
	}

	getHorizontalPosition(): number {
		return this.horizontalPosition;
	}

	getCanOpenUpward(): boolean {
		return this.canOpenUpward;
	}

	private calculateCords(element: ElementRef, selectOptionsGeometry: SelectOptionsGeometry): void {

		const elementRect = element.nativeElement.getBoundingClientRect(),
			elementBottom = this.window.pageYOffset + elementRect.bottom,
			elementLeft = this.window.pageXOffset + elementRect.left;

		this.horizontalPosition = elementLeft;
		this.verticalPosition = elementBottom - SelectOptionsCords.BORDER_WIDTH;
		this.calculateDirection(selectOptionsGeometry, element);
	}

	private calculateDirection(selectOptionsGeometry: SelectOptionsGeometry, element: ElementRef): void {
		const windowHeight = this.window.innerHeight + this.window.pageYOffset,
			elementHeight = element.nativeElement.offsetHeight,
			selectOptionsHeight = selectOptionsGeometry.getHeight();

		const selectOptionsDoesNotFitVertically = (windowHeight - this.verticalPosition - selectOptionsHeight) < 0;

		if (selectOptionsDoesNotFitVertically) {
			this.canOpenUpward = true;
			this.verticalPosition -= selectOptionsHeight + elementHeight - SelectOptionsCords.BORDER_WIDTH;
		}
	}

}
